@import url("https://fonts.googleapis.com/css2?family=Red+Hat+Display:wght@500;700;900&display=swap");
:root {
  --font-body:"Red Hat Display", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --text-color:gray;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  padding: 4.375rem 1.875rem;
  font-family: var(--font-body);
  scroll-behavior: smooth;
}
@media (max-width: 39.9375em) {
  body {
    padding: 0;
  }
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: underline;
}

.container {
  padding: 0 1.5rem;
  max-width: 69.375rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

main {
  height: 50.625rem;
  width: 81.25rem;
  background-image: url("../../../images/pattern-background-desktop.svg");
  background-repeat: no-repeat;
  border-radius: 0.625rem;
  display: flex;
  justify-content: center;
  overflow: hidden;
  padding: 4.375rem 0;
}
@media (max-width: 39.9375em) {
  main {
    width: 100%;
    padding: 0.625rem;
    margin: 0 auto;
  }
}
@media (max-width: 71.8125em) {
  main {
    width: 100%;
    height: 100vh;
  }
}

.container {
  border-radius: 0.9375rem;
  overflow: hidden;
}
@media (max-width: 39.9375em) {
  .container {
    padding: 0;
  }
}

.card {
  background-color: white;
  box-shadow: 0 0 5px 5px rgba(0, 0, 0, 0.116);
  width: 25.3125rem;
  height: 43.75rem;
}
.card a {
  color: #3829e0;
}
@media (max-width: 39.9375em) {
  .card {
    width: 21.25rem !important;
    height: 43.125rem !important;
  }
}
@media (max-width: 71.8125em) {
  .card {
    width: 28.125rem;
    height: 49.375rem;
    margin: auto 0;
  }
}
.card__title {
  font-weight: 900;
  margin-top: 2.5rem;
  margin-bottom: 1.875rem;
  text-align: center;
  font-size: 1.5625rem;
}
.card__summary {
  text-align: center;
  font-weight: 500;
  color: var(--text-color);
  margin-bottom: 2.8125rem;
}
.card__plan {
  background-color: #f5f7ff;
  display: flex;
  position: relative;
  margin: 0 auto;
  border-radius: 0.3125rem;
  width: 20.125rem;
  height: 5.125rem;
  justify-content: space-between;
  align-items: center;
}
.card__plan--image {
  padding-left: 1.25rem;
}
.card__plan--description {
  position: absolute;
  font-weight: 500;
  left: 25%;
}
.card__plan--description--cost {
  color: gray;
}
.card__plan--change {
  padding-right: 1.25rem;
}
.card__payment {
  margin-top: 2.5rem;
}
.card__payment--btn {
  background-color: #3829e0;
  border-radius: 0.625rem;
  width: 20rem;
  height: 2.75rem;
  margin: 0 auto;
  box-shadow: 0px 5px 10px #8179d8;
  display: flex;
  justify-content: center;
  align-items: center;
}
.card__payment--btn a {
  color: white;
  text-decoration: none;
  font-weight: 700;
}
.card__payment--cancel {
  padding-top: 1.875rem;
  text-align: center;
}
.card__payment--cancel a {
  color: gray;
  text-decoration: none;
}